"use client";

import Link from "next/link";
import { useRouter } from "next/navigation";
import { useForm } from "react-hook-form";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { AuthShell } from "@/components/auth/auth-shell";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

const registerSchema = z.object({
  name: z.string().min(3, "Informe seu nome completo"),
  email: z.email("Informe um e-mail válido"),
  profession: z.string().min(2, "Informe sua profissão"),
  password: z
    .string()
    .min(8, "Senha deve ter no mínimo 8 caracteres")
    .regex(/[A-Z]/, "Senha precisa ter pelo menos uma letra maiúscula")
    .regex(/[0-9]/, "Senha precisa ter pelo menos um número"),
  acceptedTerms: z.boolean().refine((value) => value, {
    error: "Você precisa aceitar os termos para continuar",
  }),
});

type RegisterInput = z.infer<typeof registerSchema>;

export default function RegisterPage() {
  const router = useRouter();
  const form = useForm<RegisterInput>({
    resolver: zodResolver(registerSchema),
    defaultValues: {
      name: "",
      email: "",
      profession: "",
      password: "",
      acceptedTerms: false,
    },
  });

  const onSubmit = async () => {
    await new Promise((resolve) => setTimeout(resolve, 500));
    router.push("/onboarding");
  };

  return (
    <AuthShell
      title="Criar conta profissional"
      subtitle="Configure sua conta KoreVitta para iniciar o onboarding"
      footer={
        <p>
          Já possui conta?{" "}
          <Link href="/login" className="underline underline-offset-4">
            Entrar
          </Link>
        </p>
      }
    >
      <form className="space-y-4" onSubmit={form.handleSubmit(onSubmit)} noValidate>
        <div className="space-y-2">
          <Label htmlFor="name">Nome completo</Label>
          <Input id="name" placeholder="Seu nome" {...form.register("name")} />
          {form.formState.errors.name ? (
            <p className="text-sm text-destructive">{form.formState.errors.name.message}</p>
          ) : null}
        </div>

        <div className="space-y-2">
          <Label htmlFor="email">E-mail</Label>
          <Input id="email" type="email" placeholder="voce@dominio.com" {...form.register("email")} />
          {form.formState.errors.email ? (
            <p className="text-sm text-destructive">{form.formState.errors.email.message}</p>
          ) : null}
        </div>

        <div className="space-y-2">
          <Label htmlFor="profession">Profissão</Label>
          <Input id="profession" placeholder="Nutricionista, preparador físico..." {...form.register("profession")} />
          {form.formState.errors.profession ? (
            <p className="text-sm text-destructive">{form.formState.errors.profession.message}</p>
          ) : null}
        </div>

        <div className="space-y-2">
          <Label htmlFor="password">Senha</Label>
          <Input id="password" type="password" placeholder="********" {...form.register("password")} />
          {form.formState.errors.password ? (
            <p className="text-sm text-destructive">{form.formState.errors.password.message}</p>
          ) : null}
        </div>

        <label className="flex items-start gap-2 text-sm text-muted-foreground">
          <input
            type="checkbox"
            className="mt-1 h-4 w-4 rounded border border-input"
            {...form.register("acceptedTerms")}
          />
          <span>Aceito os termos de uso e a política de privacidade.</span>
        </label>
        {form.formState.errors.acceptedTerms ? (
          <p className="text-sm text-destructive">{form.formState.errors.acceptedTerms.message}</p>
        ) : null}

        <Button type="submit" className="w-full" disabled={form.formState.isSubmitting}>
          {form.formState.isSubmitting ? "Criando conta..." : "Criar conta"}
        </Button>
      </form>
    </AuthShell>
  );
}
